
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

body{
    background-color: #fff;
    min-width: 320px;
    margin: 0;
    height: 100vh;
}
h1, h2 {
  font-weight: normal;
}
h3{
    font-size: 28px;
    margin-top: 15px
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.container{
    max-width: 800px;
    margin: 0px auto;
    padding: 10px;
}
input.default{
    margin: 5px;
    border: 1px solid #777;
    border-radius: 4px;
    padding: 10px 10px;
    font-size: 16px;
    -webkit-box-shadow: rgba(0,0,0,.05) 0 5px 20px;
            box-shadow: rgba(0,0,0,.05) 0 5px 20px;
    letter-spacing: 0;
    width: 205px;
    color: #333;
    font-weight: 500;
}
.btn{
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    overflow: hidden;
    margin: 5px;
    padding: 10px 12px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-transition: all 150ms linear;
    transition: all 150ms linear;
    text-align: center;
    white-space: nowrap;
    text-decoration: none !important;
    text-transform: none;
    text-transform: capitalize;
    color: #fff;
    border: 0 none;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.3;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-shadow: 2px 5px 10px #e4e4e4;
            box-shadow: 2px 5px 10px #e4e4e4;
}
.btn:hover {
    -webkit-transition: all 150ms linear;
    transition: all 150ms linear;
    opacity: .85;
}
.btn:disabled{
    opacity: .65;
    cursor: default;
}
.btn.btn-search{
    background: #416dea;
}
.input-field{
    display: inline-block;
}
.error{
    color: #ff0202;
    display: table;
    height: 20px;
}
@media (max-width: 500px) {
.input-field{display: block;
}
}

.airline{
	text-align: center;
	display: inline-block;
	max-width: 320px;
	cursor: pointer;
}
.airline-logo{
	float: left;
}
.airline-name{
	float: left;
	margin-top: 17px;
	margin-left: 15px;
}
.offer-container{
	max-height: 200px;
	overflow: auto;
}
table{
	width: 100%;
}
table tr td{
	border: 1px solid #ddd;
}
th.sort{
	cursor: pointer;
}
